header {
  position:sticky;
  background-color: #F55A5A;
  color: white;
  text-align: center;
  padding: 1.5em 0;
  font-size: 1.2em;
}

.app-journal {
  width: 80vw;
  margin:auto;
}

.journal-entry {
  margin-top: 5em;
 display:grid;
 grid-template-columns: 1fr 3fr;
 grid-gap:1em;
}

.journal-img {
  border-radius: 6px;
    max-width:100%;
    object-fit: contain;
    overflow: clip;

}

h1 {
  font-size: 2em;
  font-weight: bold;
  margin-top: 5px;
}
h2 {
  letter-spacing: 3px;
  text-transform:uppercase;
  font-size: 1em;
  display: inline;
  margin-right: 2em;
  color: #2B283A;
}

.map-link {
  /* text-decoration: none; */
  color:#918E9B;
  display: inline;

}

.description {
  line-height: 150%;
}

